.reviews .swiper {
  width: 100%;
  margin: 0;

  @media (max-width: 1600px) {
    width: 790px;
  }

  @include vp-1199 {
    width: 560px;
  }

  @include vp-767 {
    width: 290px;
  }
}

.reviews h2 {
  font-size: 35px;
  font-weight: 800;
  line-height: 35px;
  align-self: end;
  position: relative;

  @include font-background("");

  @include vp-1199 {
    font-size: 25px;
    line-height: 25px;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 22px;
    padding-bottom: 3px;
  }
}

.reviews__inner-wrap {
  display: flex;
  justify-content: space-between;
  margin-bottom: 26px;

  @include vp-767 {
    margin-bottom: 30px;
  }
}

.reviews__buttons {
  display: flex;
  padding: 7px 0 14px;
  gap: 4px;

  @include vp-1199 {
    padding: 7px 0 6px;
  }

  @include vp-767 {
    padding: 0;
  }
}
